.products {
  background-color: $color-dark-text;
  padding-top: 70px;
  padding-bottom: 138px;

  @include vp-1023 {
    padding-top: 60px;
    padding-bottom: 75px;
  }

  @include vp-767 {
    padding-top: 50px;
    padding-bottom: 86px;
  }
}

.products__container {
  max-width: 1302px;
}

.products__title {
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  color: $color-default-white;
  margin: 0;
  margin-bottom: 52px;

  @include vp-1023 {
    margin-bottom: 50px;
  }

  @include vp-767 {
    font-size: 28px;
    line-height: 39px;
    margin-bottom: 40px;
  }
}

.products__text {
  &--mobile {
    display: none;

    @include vp-767 {
      display: block;
    }
  }

  &--not-mobile {
    @include vp-767 {
      display: none;
    }
  }
}

.products__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 85px;
  justify-content: space-between;
  max-width: 1302px;
  padding: 0;
  margin: 0 auto;
  list-style: none;

  @include vp-1023 {
    grid-template-columns: 1fr;
    gap: 30px;
  }
}
